<template>
	<div>
		<p class="jisp">Banner</p>	
	    <div class="div">
			<el-row>
				<el-col :span="24">
					<div class="label">
						<label></label>
				    	<el-select v-model="type" slot="prepend" placeholder="" style="width: 100px" size="small" @change="changeMethod">
					         <el-option label="" value=""></el-option>
					        <el-option label="" value=""></el-option>
					        <el-option label="" value=""></el-option>
					    </el-select>
					</div>
					<el-row>
						<el-col :span="24">
							<div class="upload_label">
								<label></label>
								<div class="file" id="Tematichesr">
						            <input type="file" id="zuye" name="file" @change="previewFile"/>
						        </div>
							</div>
						</el-col>
					</el-row>
					<el-row class="label">
						<el-col :span="24">
							<label></label>
							<el-button size="small" @click="addBanners" :disabled="addbanner" v-if="!controlButton"></el-button>
							<el-row v-if="controlButton">
								<el-col :span="16" v-if="specialList && !worksList && !artistList">
									<el-table
										border
										stripe
									    :data="centent"
									    style="width: 100%">
									    <el-table-column
									      prop="coverUrl"
									      label="" >
									      <template scope="scope">
										       <img :src="scope.row.coverUrl" class="img" alt="">
									        </template>
									    </el-table-column>
									    <el-table-column
									      prop="id"
									      label="ID"
									      width="100">
									    </el-table-column>
									    <el-table-column
									      prop="title"
									      label=""
									      width="100">
									    </el-table-column>
									     <el-table-column
									      prop="createuserName"
									      label=""
									      width="110">
									    </el-table-column>
									     <el-table-column
									      prop="type"
									      label=""
									      width="160">
									    </el-table-column>
									     <el-table-column
									      prop="createTime"
									      label=""
									      width="100">
									    </el-table-column>
									     <el-table-column
									      prop="cmsEditoruserName"
									      label=""
									      width="100">
									    </el-table-column>
									    <el-table-column
									      prop="updateTime"
									      label=""
									      width="100">
									    </el-table-column>
									    <el-table-column
									      prop="dataState"
									      label=""
									      width="100">
									    </el-table-column>
									    <el-table-column label="" width="100%"  fixed="right">
									        <template scope="scope">
									            <el-button size="small" @click="deleteButton"></el-button>
									        </template>
									    </el-table-column>
									</el-table>
								</el-col>
								<el-col :span="24" class="table" v-if="!specialList && worksList && !artistList">
							    	<el-table
										<el-table
										border
										highlight-current-row
									    :data="centent"
									    style="width: 100%">
									    <el-table-column
									      prop="id"
									      label="ID"
									       width="120">
									    </el-table-column>
									    <el-table-column
									      prop="title"
									      label=""
									      width="120">
									    </el-table-column>
									    <el-table-column
									      prop="userName"
									      label=""
									      width="120">
									    </el-table-column>
									     <el-table-column
									      prop="releaseTime"
									      label=""
									      width="150">
									    </el-table-column>
									     <el-table-column
									      prop="type"
									      label="/"
									      width="120">
									    </el-table-column>
									     <el-table-column
									      prop="tagst"
									      label=""
									     width="120">
									    </el-table-column>
									     <el-table-column
									      prop="type"
									      label=""
									      width="120">
									    </el-table-column>
									    <el-table-column
									      prop="description"
									      label=""
									       width="120">
									    </el-table-column>
									    <el-table-column
									      prop="audioName"
									      label=""
									      width="180">
									    </el-table-column>
									    <el-table-column
									      prop="state"
									      label="/"
									       width="120">
									    </el-table-column>
									    <el-table-column
									      prop="play"
									      label=""
									       width="120">
									    </el-table-column>
									    <el-table-column
									      prop="store"
									      label=""
									       width="120">
									    </el-table-column>
									    <el-table-column
									      prop="comment"
									      label=""
									      width="120">
									    </el-table-column>
									    <el-table-column
									      prop="forward"
									      label=""
									      width="120">
									    </el-table-column>
									    <el-table-column
									      prop="videoAppVersion"
									      label="APP"
									      width="120">
									    </el-table-column>
									    <el-table-column
									      prop="videoAppOs"
									      label=""
									       width="120">
									    </el-table-column>
									    <el-table-column
									      prop="dataState"
									      label=""
									      width="120">
									    </el-table-column>
									    <el-table-column label="" width="100"  fixed="right">
									        <template scope="scope">
										        <el-button
										          size="small"
										          @click="deleteButton"></el-button>
									        </template>
									    </el-table-column>
									</el-table>
						    	</el-col>
						    	<el-col :span="18"  class="table" v-if="!specialList && !worksList && artistList">
					    			<el-table
										border
									    :data="centent"
									    style="width: 100%">
									    <el-table-column
									      prop="headPortrait"
									      label=""
									      width="100%">
									        <template scope="scope">
										       <img :src="scope.row.avatarUrl" class="img" alt="">
									        </template>
									    </el-table-column>
									    </el-table-column>
									    <el-table-column
									      prop="userId"
									      label="ID"
									      width="100%">
									    </el-table-column>
									    <el-table-column
									      prop="userName"
									      label=""
									      width="100%">
									    </el-table-column>
									     <el-table-column
									      prop="gender"
									      label=""
									      width="100%">
									    </el-table-column>
									     <el-table-column
									      prop="phoneNumber"
									      label=""
									      width="130%">
									    </el-table-column>
									     <el-table-column
									      prop="bio"
									      label=""
									      width="100%">
									    </el-table-column>
									     <el-table-column
									      prop="createTime"
									      label=""
									      width="170%">
									    </el-table-column>
									    <el-table-column
									      prop="updateTime"
									      label=""
									      width="170%">
									    </el-table-column>
									    <el-table-column
									      prop="identity"
									      label=""
									      width="100%">
									    </el-table-column>
									    <el-table-column
									      prop="userLevel"
									      label=""
									      width="100%">
									    </el-table-column>
									    <el-table-column label="" width="100%" fixed="right">
									        <template scope="scope">
										        <el-button
										          size="small"
										          @click="deleteButton"></el-button>
									        </template>
									    </el-table-column>
									</el-table>
					    		</el-col>
							</el-row>
								
						</el-col>
					</el-row>
					<el-dialog
						title="" size="large"
						:visible.sync="popControl">
						<special v-if="specialList && !worksList && !artistList" :popControl="popControl" @PopController="getdialogVisibles"/>
						<works v-if="!specialList && worksList && !artistList"  :popControl="popControl" @PopController="getdialogVisibles"/>
						<artist v-if="!specialList && !worksList && artistList" :popControl="popControl" @PopController="getdialogVisibles"/>
					</el-dialog>
					<el-button size="small" @click="getBanner"></el-button>
					<el-button size="small" @click="cancelButton"></el-button>
				</el-col>
			</el-row>
		</div>
	</div>
</template>
<script> 
	import {mapMutations,mapActions, mapState} from "vuex"
	import {SWITCHone,SWITCHtwo, SWITCHthree} from "../../../../store/operationManagement/addBanner.js"
	import {controlSWITCHtwo} from "../../../../store/operationManagement/bannerList.js"
	import special from "./BannerClass/special.vue"
	import works from "./BannerClass/works.vue"
	import artist from "./BannerClass/artist.vue"
	//
	import getLocalTime from "../../../../store/getLocalTimes.js"

	//MD5
	import Md5 from 'js-md5';
	import browserMD5File  from 'browser-md5-file'
	export default{
		name:"selectedList",

		//HTML
		beforeMount(){
			var obj ={}
			this.type = this.EDITBanner.type
			//
			if (this.type !=="") {
		    		this.addbanner = false
		    	}	

			if (this.type ==="") {
				this.getControlButton()
				this.SWITCHone()
				console.log(this.EDITBanner)
				let obj ={
					//
					coverUrl:this.EDITBanner.topicInfo.coverUrl,
					//id
					id:this.EDITBanner.topicInfo.id,
					//
					createTime:getLocalTime(this.EDITBanner.topicInfo.createTime),
					//
					title:this.EDITBanner.topicInfo.title,
					//
					createuserName:this.EDITBanner.topicInfo.cmsCreatorInfo.userName,
					//
					cmsEditoruserName:this.EDITBanner.topicInfo.cmsEditorInfo === undefined ? "" :this.EDITBanner.topicInfo.cmsEditorInfo.userName,
					//
					updateTime:getLocalTime(this.EDITBanner.topicInfo.updateTime),
					//
					//012
					dataState:this.EDITBanner.topicInfo.dataState === 0 ? "" : this.EDITBanner.topicInfo.dataState === 1? "" : "",
					//
					//01
					type:this.EDITBanner.topicInfo.type === 0 ? "" : "",
				}
				this.getCemtent(obj)
			}
			if (this.type ==="") {
				let obj ={
					//id
					id : this.EDITBanner.worksInfo.id,
					//
					title : this.EDITBanner.worksInfo.title,
					//
					userName : this.EDITBanner.worksInfo.userBaseInfo.userName,
					// avatarUrl: item.userBaseInfo.avatarUrl,
					// userId: item.userBaseInfo.userId,
					//
					releaseTime : getLocalTime(this.EDITBanner.worksInfo.releaseTime),
					///01
					type : this.EDITBanner.worksInfo.type === 0 ?  "" : "",
					//
					tags:this.EDITBanner.worksInfo.tags === undefined ? [] : this.EDITBanner.worksInfo.tags ,
					//
					// address : item.
					//
					description : this.EDITBanner.worksInfo.description,
					//
					audioName : this.EDITBanner.worksInfo.type === 0 ? this.EDITBanner.worksInfo.videoInfo.audioInfo.audioName : this.EDITBanner.worksInfo.audioInfo.audioInfo.audioName,
					///01
					state : this.EDITBanner.worksInfo.state === 0 ? "" : "",
					//
					play : this.EDITBanner.worksInfo.type === 0 ? this.EDITBanner.worksInfo.videoInfo.scoreInfo.play : this.EDITBanner.worksInfo.audioInfo.scoreInfo.play,
					//
					store : this.EDITBanner.worksInfo.type === 0 ? this.EDITBanner.worksInfo.videoInfo.scoreInfo.store : this.EDITBanner.worksInfo.audioInfo.scoreInfo.store,
					//
					comment : this.EDITBanner.worksInfo.type === 0 ? this.EDITBanner.worksInfo.videoInfo.scoreInfo.comment : this.EDITBanner.worksInfo.audioInfo.scoreInfo.comment,
					//
					forward : this.EDITBanner.worksInfo.type === 0 ? this.EDITBanner.worksInfo.videoInfo.scoreInfo.forward : this.EDITBanner.worksInfo.audioInfo.scoreInfo.forward, 
					//App 
					videoAppVersion : this.EDITBanner.worksInfo.type === 0 ? this.EDITBanner.worksInfo.videoInfo.videoAppVersion : "", 
					// 
					videoAppOs: this.EDITBanner.worksInfo.type === 0 ? this.EDITBanner.worksInfo.videoInfo.videoAppOs === 0 ? "Android" : "IOS" : "", 
					//o12
					dataState : this.EDITBanner.worksInfo.dataState === 0 ? "" :  this.EDITBanner.worksInfo.dataState === 1 ? "" : "",
					//
					worksId : this.EDITBanner.worksInfo.type === 0 ?  this.EDITBanner.worksInfo.videoInfo.worksId : this.EDITBanner.worksInfo.audioInfo.worksId, 
					//
					urlCover:this.EDITBanner.worksInfo.urlCover,
					userBaseInfo:this.EDITBanner.worksInfo.userBaseInfo
				}
				let arr =[]
				obj.tags.forEach((element) => {
					arr.push(element.name)
				});
				obj.tagst = arr.join(",")
				this.getControlButton()
				this.SWITCHtwo()
				this.getCemtent(obj)
			}
			if (this.type ==="") {
				let obj={	
					//
					avatarUrl:this.EDITBanner.userInfo.avatarUrl,
					//ID
					userId:this.EDITBanner.userInfo.userId,
					//
					userName:this.EDITBanner.userInfo.userId,
					//012
					gender:this.EDITBanner.userInfo.gender === 0 ? "" : this.EDITBanner.userInfo.gender === 1 ? "" : "",
					//
					userName:this.EDITBanner.userInfo.userName,
					//
					phoneNumber:this.EDITBanner.userInfo.phoneNumber,
					//
					bio:this.EDITBanner.userInfo.bio === undefined ? "" : this.EDITBanner.userInfo.bio,
					//
					createTime: getLocalTime(this.EDITBanner.userInfo.createTime),
					//
					updateTime:getLocalTime(this.EDITBanner.userInfo.updateTime),
					//
					//
				}
				this.getControlButton()
				this.SWITCHthree()
				this.getCemtent(obj)
			}
		},
		mounted(){

			let preview = document.getElementById("Tematichesr")
			preview.style.backgroundImage= `url(${this.EDITBanner.coverUrl})`;
			this.GETURL(this.EDITBanner.coverUrl)
		},
		components: {
			special,
			works,
			artist
		},
		computed:{
	      ...mapState("addBanner",["specialList","worksList","artistList","dialogVisible","centent","controlButton","url"]),
	      ...mapState("bannerList",["EDITBanner"])
	    },
		data(){
			return{
				imageUrl: '',
				 //
				type:"",
				//
				addbanner:true,
				//
				popControl:false,
				dialogVisibles:false,
			}
		},
		methods:{
			...mapActions("addBanner",["GETUPLOADIP","GETTUPIAN","GETBNANER"]),
			...mapMutations("addBanner",["SWITCHone","SWITCHtwo","SWITCHthree","SWITCHDELETE","uploadPicture","dataURItoBlob","setControlButton","setCentent","GETURL","getControlButton","getCemtent"]),
			...mapMutations("bannerList",["controlSWITCHtwo"]),
        	handleAvatarSuccess(res, file) {
		        this.imageUrl = URL.createObjectURL(file.raw);
		    },
		    getdialogVisibles(data){
		    	this.popControl =data
		    },
		    changeMethod(){
		    	this.setControlButton()
		    	this.setCentent()
		    	if (this.type !=="") {
		    		this.addbanner = false
		    	}
		    },
		    addBanners(){
		    	this.popControl = true
		    	// this.ControlButton()
		    	switch(this.type) {
		    		case "":
		    			this.SWITCHone()
		    			break;
		    		case "":
		    			this.SWITCHtwo()
		    			break;
		    		case "":
		    			this.SWITCHthree()
		    			break;
		    	}
		    },
		    //
		    deleteButton(){
		    	this.setCentent()
		    	this.setControlButton()
		    },
		    getBanner(){
		    	console.log(this.type)
		    	var preview = document.getElementById("Tematichesr")
		    	let id = null

		    	if (this.type === "") {
		    		this.$message({
			          showClose: true,
			          message: '',
			          type: 'warning'
			        });
			       	return
		    	}
		    	if (this.url === "") {
		    		this.$message({
			          showClose: true,
			          message: '',
			          type: 'warning'
			        });
			       	return
		    	}
		    	if (this.centent.length === 0) {
		    		this.$message({
			          showClose: true,
			          message: '',
			          type: 'warning'
			        });
			       	return
		    	}
		    	if (this.centent[0].id !== undefined && this.centent[0].userId ===undefined) {
						id = this.centent[0].id
				}
				if (this.centent[0].id === undefined && this.centent[0].userId !==undefined) {
					id = this.centent[0].userId
				}
				if (this.type === "") {this.type = 0}
				if (this.type === "") {this.type = 1}
				if (this.type === "") {this.type = 2}
		    	this.GETBNANER({
		    		type:this.type,
		    		contentId:id,
		    		coverUrl:this.url,
		    	})
		    	this.setCentent()
		    	this.type =""
		    	preview.style.backgroundImage= "";
		    	this.SWITCHDELETE()
		    	this.controlSWITCHtwo()
		    },
		    //
		    cancelButton(){
		    	let preview = document.getElementById("Tematichesr")
		    	this.setCentent()
		    	this.type =""
		    	preview.style.backgroundImage= "";
		    	this.SWITCHDELETE()
		    	this.controlSWITCHtwo()
		    },


		    //
		    previewFile(event) {
        		var thist =this
        	    let UploadPicture={}
		       var file = event.target.files[0];
		      	var  readers = new FileReader()
		      	var reader = new FileReader();
				reader.onload = function(event) {
					preview.style.backgroundImage= `url(${reader.result})`;
				};
				readers.onload = function(event) {
					thist.dataURItoBlob(readers.result)
				};
				if (file) {
		       	reader.readAsDataURL(file);
		      		readers.readAsArrayBuffer(file)
		       }
		      	var preview = document.getElementById("Tematichesr")
		      	browserMD5File(file, function (err, md5) {
				    UploadPicture.md5s = md5
				    UploadPicture.fsize = file.size
		      		UploadPicture.fileext = file.type
		      		UploadPicture.sign = Md5(`${UploadPicture.md5s}key`)
		      		thist.uploadPicture(UploadPicture)
		      		thist.GETTUPIAN()
				});
			      	
			},
		}
	}
</script>
<style scoped>
	.file,.centent{
	    background-color: #fbfdff;
	    border: 1px dashed #c0ccda;
	    border-radius: 6px;
	    box-sizing: border-box;
	    width: 300px;
	    height: 150px;
	    cursor: pointer;
	    line-height: 146px;
	    vertical-align: top;
	    background-size:100% 100% ;
    }
    .file input{
	    position: relative;
	    top: -70px;
	    height: 100%;width: 100%;
	    opacity: 0;
	    cursor: pointer;
	}
	.center{
		margin-top: 20px;
		text-align: center;
	}
	.label{
		margin-bottom: 20px;
	}
	.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #20a0ff;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 100px;
    line-height: 100px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  .upload_label{
  	display: flex;
  	margin-bottom: 20px;
  }
  .upload_label >*{
  	margin-right: 20px;
  }
  .upload_label >label{
  	margin-right: 20px;
  }
  .table{
		padding-top: 20px;
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
	}
	.img{
		height: 50px;width: 50px;margin-top: 6px;
	}
	.jisp{
		margin: 0;
		height: 70px;
		color: #353535;
		font-size: 20px;
		line-height: 70px;
		background-color: #f2f2f2;
		padding-left: 30px;
		margin-bottom: 25px;
	}
	.div{
		padding:0 30px;
	}
</style>


